.paperItem {
  height: 200px;
  width: 400px;
  padding: 10px;
  display: flex;
  margin: 20px;

  .img {
    width: 200px;
  }

  .description {
    width: 80%;
    padding: 10px;
    display: flex;
    flex-direction: column;


    & span {
      padding: 10px;
    }

    .buttons {
      display: flex;
      justify-content: space-around;
      margin-top: 50px;

      & button {
        width: 60px;
      }
    }
  }
}
